@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
#nav {
    display: flex;
    width: 96%;
    padding: 2%;
    background: var(--background);
    flex-flow: row wrap;
    justify-content: space-between;
}

#links {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
}

.navLink {
    color: var(--titlecolor);
    font-size: 2em;
    text-decoration: none;
}

#view {
    display: flex;
    width: 100%;
    height: 80vh;   /* TODO: Make this a height determined dynamically. */
    align-items: center;
    justify-content: center;
}

@media screen and (max-width: 400) {
    #nav {
        flex-flow: column wrap;
    }

    .navLink {
        margin: .5em;
    }

    #view {
        height: auto;
    }
}
#suggestions {
    width: 80vw;
    height: 100%;
    text-align: center;
}

#suggestions input {
    width: 100%;
    height: var(--defaultFontSize);
    padding: .2em;
    border: none;
    border-bottom: thin solid black;
    -webkit-transition: border-bottom .1s linear;
    transition: border-bottom .1s linear;
    font-size: var(--defaultFontSize);
}
#suggestions input:focus {
    border-bottom: solid var(--background);
}

#names * {
    color: var(--black);
}
#names * + * {
    border-top: thin solid var(--black);
}

.suggestion {
    display: block;
    width: 100%;
    z-index: 5;
    text-decoration: none;
    padding: .2em;
    color: var(--black);
    font-size: 2em;
    -webkit-transition: all .3s linear;
    transition: all .3s linear;
}
.suggestion:hover {
    background: var(--darkeroffwhite);
}

.noResults {
    background: var(--warningRed);
}
#results {
    display: table;
    width: 90%;
    font-size: var(--defaultFontSize);
    border: 1px solid black;
}

#results tr:nth-child(even) {
    background-color: var(--darkeroffwhite);
}

#results a {
    display: table-cell;
    color: var(--black);
    -webkit-text-decoration: underline dotted black;
            text-decoration: underline dotted black;
}
/***************
Checkbox styling
***************/
.checkbox {
    display: block;
    position: relative;
    width: 1em;
    height: 1em;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.checkbox input {
    position: absolute;
    left: 0;
    top: 0;
    margin: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    z-index: 5;
    cursor: pointer;
}

/* Styles under all conditions */
.checkbox input ~ .checkmark {
    position: absolute;
    left: 0;
    top: 0;
    margin: 0;
    width: 100%;
    height: 100%;
    background: #FFAB91;
    -webkit-transition: background .3s linear, box-shadow .3s linear;
    transition: background .3s linear, box-shadow .3s linear;
}

/* Styles on hover */
.checkbox input:hover ~ .checkmark {
    box-shadow: var(--defaultboxshadow);
}

/* Styles when checked */
.checkbox input:checked ~ .checkmark {
    background: var(--activeBlue);
    box-shadow: var(--defaultboxshadow);
}

.checkbox input:checked ~ .checkImage {
    opacity: 1;
}

/**************
Counter styling
**************/
.counter {
    display: flex;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    flex-flow: row wrap;
    align-items: baseline;
}

.counter button {
    height: 4rem;
    width: 4rem;
    border: none;
    padding: 2%;
    text-align: center;
    text-decoration: none;
    font-size: .75em;
    -webkit-transition: background .3s linear;
    transition: background .3s linear;
}

.count {
    background: var(--offwhite);
}

.minus {
    background: #FFAB91;
}

.minus:hover {
    background: #FF8A65;
}

.plus {
    background: #A5D6A7;
}

.plus:hover {
    background: #81C784;
}

/**********************
Checkin webpage styling
**********************/
#checkin {
    display: grid;
    width: 95%;
    height: 95%;
    grid-template-columns: 50% 50%;
    grid-template-rows: 65% 35%;
    justify-items: center;
    align-items: center;
    justify-content: space-around;
    align-content: center;
    padding: 2.5%;
    font-size: 2.5em;
}

.namesTable {
    width: 90%;
    height: 90%;
    border: thin solid black;
}

#checkinButton {
    width: 90%;
    height: 90%;
    background: var(--background);
    color: var(--offwhite);
    padding: 5%;
    border-radius: 35px;
    font-size: 3rem;
    -webkit-transition: box-shadow .3s linear, background .3s linear;
    transition: box-shadow .3s linear, background .3s linear;
}

#checkinButton:active {
    box-shadow: var(--defaultboxshadow);
    background: var(--activeBlue);
}

@media screen and (max-width: 500px) {
    #checkin {
        display: flex;
        flex-flow: column wrap;
        width: 100%;
        height: auto;
    }
}
#admin {
    display: flex;
    flex-flow: row wrap;
    width: 100%;
    height: 100%;
}
/* Globals
 * Color schemes, global resets, etc.
 */

:root {
    --titlecolor: #f6f6f6;
    --black: #101010;
    --background: #7ea7d8;
    --offwhite: #fafafa;
    --darkeroffwhite: #f5f5f5;
    --inactiveBlue: rgb(115, 180, 174);
    --warningRed: #ffebee;
    --activeBlue: #80CBC4;
    --defaultboxshadow: 0 2px 10px 2px rgba(0,0,0,.12);
    --defaultFontSize: 3em;
}

* {
    margin: 0;
    padding: 0;
    color: #101010;
    color: var(--black);
    text-decoration: none;
    border: none;
    background: none;
    font-family: 'Roboto', Arial, Helvetica, sans-serif;
}

@media only screen and (max-width: 400px) {
    :root {
        --defaultFontSize: 1.5em;
    }
}
