#nav {
    display: flex;
    width: 96%;
    padding: 2%;
    background: var(--background);
    flex-flow: row wrap;
    justify-content: space-between;
}

#links {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
}

.navLink {
    color: var(--titlecolor);
    font-size: 2em;
    text-decoration: none;
}

#view {
    display: flex;
    width: 100%;
    height: 80vh;   /* TODO: Make this a height determined dynamically. */
    align-items: center;
    justify-content: center;
}

@media screen and (max-width: 400) {
    #nav {
        flex-flow: column wrap;
    }

    .navLink {
        margin: .5em;
    }

    #view {
        height: auto;
    }
}